/**
 * 格式化时间戳
 * @param timeStamp 时间戳
 * @param format 格式化后的格式，默认'YY-MM-DD hh:mm:ss'
 * @returns 格式化后的时间
 */
export function formatDate(
    timeStamp: number,
    format: string = 'YY-MM-DD hh:mm:ss',
) {
    const date = new Date(timeStamp);

    const year = date.getFullYear(),
        month = date.getMonth() + 1, //月份是从0开始的
        day = date.getDate(),
        hour = date.getHours(),
        min = date.getMinutes(),
        sec = date.getSeconds(),
        milliSec = date.getMilliseconds();
    const preArr = Array.apply(null, Array(10)).map(function (elem, index) {
        return '0' + index;
    });

    const newTime = format
        .replace(/YY/g, String(year))
        .replace(/MM/g, String(preArr[month] || month))
        .replace(/DD/g, String(preArr[day] || day))
        .replace(/hh/g, String(preArr[hour] || hour))
        .replace(/mm/g, String(preArr[min] || min))
        .replace(/ss/g, String(preArr[sec] || sec))
        .replace(/ms/g, String(preArr[milliSec] || milliSec));

    return newTime;
}
